import axios from "../utils/axios";

export default class service {

    static baseURL = 'location';

    static detailState(id, data) {
        return axios.get(this.baseURL + '/state/details/' + id, { params: data });
    }
    static listState(data) {
        return axios.get(this.baseURL + '/state/list', { params: data });
    }
    static saveState(data, query) {
        return axios.post(this.baseURL + "/state/save", data, { params: query });
    }
    static deleteState(id) {
        return axios.post(this.baseURL + '/state/delete', { ids: id });
    }

    static detailDistrict(id, data) {
        return axios.get(this.baseURL + '/district/details/' + id, { params: data });
    }
    static listDistrict(data) {
        return axios.get(this.baseURL + '/district/list', { params: data });
    }
    static saveDistrict(data, query) {
        return axios.post(this.baseURL + "/district/save", data, { params: query });
    }
    static deleteDistrict(id) {
        return axios.post(this.baseURL + '/district/delete', { ids: id });
    }

    static detailCity(id, data) {
        return axios.get(this.baseURL + '/city/details/' + id, { params: data });
    }
    static listCity(data) {
        return axios.get(this.baseURL + '/city/list', { params: data });
    }
    static saveCity(data, query) {
        return axios.post(this.baseURL + "/city/save", data, { params: query });
    }
    static deleteCity(id) {
        return axios.post(this.baseURL + '/city/delete', { ids: id });
    }

}