/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Input,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Image,
  DatePicker,
} from "antd";
import {
  PlusOutlined,
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  EyeOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import service from "../../../services/music";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import commonObj from "../../../commonObj";
import UploadImage from "../../../utils/UploadImage";
import UploadVideo from "../../../utils/UploadVideo";
import moment from "moment";
const viewAccess = true;
const addAccess =
  commonObj.userType === "superAdmin" || commonObj.modules?.includes("add");
const editAccess =
  commonObj.userType === "superAdmin" || commonObj.modules?.includes("edit");
const deleteAccess =
  commonObj.userType === "superAdmin" || commonObj.modules?.includes("delete");

export default function SingerFile({ fileType, singerId = null }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({
    page: 1,
    limit: 20,
    type: fileType,
    singerId,
  });
  const addNewModalRef = useRef();

  const columns = [
    {
      title: "File",
      dataIndex: "file",
      render: (v, row) => {
        if (row.type === "photo") {
          return <Image src={v?.url} height={80} />;
        } else if (row.type === "video") {
          return (
            <video height="80" controls>
              <source src={v?.url} type="video/mp4" />
            </video>
          );
        } else if (row.type === "youtube-short") {
          return v?.url;
        } else if (row.type === "youtube-video") {
          return v?.url;
        }
      },
    },
    {
      title: "Uploaded At",
      dataIndex: "createdAt",
      render: (v, row) => {
        return moment(v).format("dddd, MMMM Do YYYY, h:mm:ss a");
      },
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
      render: (v, row) => {
        return (
          <>
            {deleteAccess ? (
              <Popconfirm
                title="Are you sure to delete this File?"
                onConfirm={() => {
                  deleteData(row._id);
                }}
                okText="Yes"
                cancelText="No"
              >
                <Button
                  type="primary"
                  size="small"
                  danger
                  icon={<DeleteOutlined />}
                />
                &nbsp;
              </Popconfirm>
            ) : null}
            {editAccess ? (
              <Button
                type="primary"
                size="small"
                icon={<EditOutlined />}
                onClick={() => {
                  addNewModalRef.current.openForm(row);
                }}
              />
            ) : null}
            {!editAccess && viewAccess ? (
              <Button
                type="primary"
                size="small"
                icon={<EyeOutlined />}
                onClick={() => {
                  addNewModalRef.current.openForm(row);
                }}
              />
            ) : null}
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);

  function list() {
    if (viewAccess) {
      setLoading(true);
      service
        .listSingerFileData(qData)
        .then((res) => {
          setData(res.data?.map((v) => ({ ...v, key: v._id })));
          setQData({
            ...qData,
            limit: res.extra.limit,
            page: res.extra.page,
            total: res.extra.total,
          });
        })
        .catch((err) => {})
        .finally(() => {
          setLoading(false);
        });
    }
  }

  const deleteData = (id) => {
    service
      .deleteSingerFileData(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };

  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";

  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };

  useEffect(() => {
    list();
  }, [qData.page, qData.limit]);

  return (
    <>
      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
      />
      <AddForm ref={addNewModalRef} {...{ list, fileType, singerId }} />
    </>
  );
}

function Search({
  addNewModalRef,
  selected,
  deleteData,
  qData,
  setQData,
  list,
}) {
  const [dataValue, setDataValue] = useState([]);
  const onChange = (v, key) => {
    setDataValue(v);
    qData[key] = v;
    if (v === undefined || v === null) {
      qData[key] = "";
      setDataValue([]);
      list();
    }

    console.log(qData);
  };
  // onChange={v => (setQData({ ...qData, Datekey: v }))}

  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]} style={{ margin: "1rem 0" }}>
        <Col span={8}>
          <Form.Item style={{ marginBottom: 0 }}>
            <DatePicker.RangePicker
              style={{ width: "100%" }}
              value={dataValue}
              onChange={(e) => onChange(e, "Datekey")}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          {addAccess ? (
            <Form.Item style={{ marginBottom: 0, float: "right" }}>
              <Button
                type="primary"
                icon={<PlusOutlined />}
                onClick={() => {
                  addNewModalRef.current.openForm();
                }}
              >
                Add New
              </Button>
            </Form.Item>
          ) : null}
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected data?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const AddForm = forwardRef((props, ref) => {
  const { list, fileType, singerId } = props;
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1, singerId });
  const [ajxRequesting, setAjxRequesting] = useState(false);
  const [changeForm, setChangeForm] = useState(false);
  const [file, setFile] = useState([]);

  const handleChange = (value) => {
    setData({ ...Date, ...value });
  };

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt ? { ...dt } : {});
      if (!dt && addAccess) {
        setChangeForm(true);
      } else if (dt && editAccess) {
        setChangeForm(true);
      } else {
        setChangeForm(false);
      }
    },
  }));

  const save = () => {
    if (changeForm) {
      setAjxRequesting(true);
      service
        .saveSingerFileData({ ...data, singerId, type: fileType })
        .then((res) => {
          AntdMsg(res.message);
          handleChange({ ...res.data });
          list();
          setOpen(false);
        })
        .catch((err) => {
          if (typeof err.message === "object") {
            let dt = err.message[Object.keys(err.message)[0]];
            AntdMsg(dt, "error");
          } else {
            AntdMsg(err.message, "error");
          }
        })
        .finally(() => {
          setAjxRequesting(false);
        });
    }
  };

  useEffect(() => {
    if (file[0]) {
      handleChange({ file: file[0] });
    }
  }, [file]);

  useEffect(() => {
    if (!open) {
      setData({ _id: null });
    } else {
      setFile([]);
    }
  }, [open]);

  return (
    <>
      <Modal
        title={(!data._id ? "Add" : "Edit") + " Data"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={600}
        className="app-modal-body-overflow"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          changeForm ? (
            <Button key="save" type="primary" onClick={save}>
              Save
            </Button>
          ) : null,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          {fileType === "photo" ? <Photo {...{ file, setFile }} /> : null}
          {fileType === "video" ? <Video {...{ file, setFile }} /> : null}
          {fileType === "youtube-short" ? (
            <YoutubeShorts {...{ data, setData }} />
          ) : null}
          {fileType === "youtube-video" ? (
            <YoutubeVideo {...{ data, setData }} />
          ) : null}
        </Spin>
      </Modal>
    </>
  );
});

const Photo = ({ file, setFile }) => {
  return (
    <>
      <Row gutter={[12, 2]}>
        <Col span={24}>
          <UploadImage
            count={1}
            {...{ fileList: file, setFileList: setFile }}
          />
        </Col>
      </Row>
    </>
  );
};
const Video = ({ file, setFile }) => {
  return (
    <>
      <Row gutter={[12, 2]}>
        <Col span={24}>
          <UploadVideo
            count={1}
            {...{ fileList: file, setFileList: setFile }}
          />
        </Col>
      </Row>
    </>
  );
};
const YoutubeShorts = ({ data, setData }) => {
  return (
    <>
      <Row gutter={[12, 2]}>
        <Col span={24}>
          <Form.Item label="Type" required>
            <Input
              placeholder="Enter Youtube short URL"
              onChange={(e) => {
                setData({
                  ...data,
                  file: { url: e.target.value, uid: "0", name: "" },
                });
              }}
            />
          </Form.Item>
        </Col>
      </Row>
    </>
  );
};

const YoutubeVideo = ({ data, setData }) => {
  useEffect(() => {
    console.log(data);
  }, [data]);

  return (
    <>
      <Row gutter={[12, 2]}>
        <Col span={24}>
          <Form.Item label="Type" required>
            <Input
              placeholder="Enter Youtube video URL"
              onChange={(e) => {
                setData({
                  ...data,
                  file: { url: e.target.value, uid: "0", name: "" },
                });
              }}
            />
          </Form.Item>
        </Col>
      </Row>
    </>
  );
};
