/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Tag } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../../services/ecomm';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import commonObj from '../../../commonObj';
import OrderDetail from './OrderDetail';
const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Order({ type, categoryId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, type, categoryId });
    const addNewModalRef = useRef();
    const attributeModalRef = useRef();


    const columns = [
        {
            title: 'Order id',
            dataIndex: '_id',
            // width: 200,
        },
        {
            title: 'Shipping Address',
            dataIndex: 'shippingAddress',
            width: 250,
        },
        {
            title: 'Payment Method',
            dataIndex: 'paymentMethod',
            width: 180,
        },
        {
            title: 'Total Amount',
            dataIndex: 'totalAmount',
            width: 150,
        },
        {
            title: 'Details',
            dataIndex: 'orderId',
            width: 150,
            render: (value, row) => (
                <Button type="primary" size="small" onClick={() => { attributeModalRef.current.openForm({ _id: row._id }) }}>Order Details</Button>
            )
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: (_,{status}) => {
                return <Tag color='green'>{status}</Tag>
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this Product?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }
                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.orderList(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.orderDelete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, categoryId }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            {
                !categoryId
                    ? <Typography.Title level={5}>List of  Orders</Typography.Title>
                    : null
            }

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, type, categoryId }} />
            <AttributeModal ref={attributeModalRef} />

        </>
    );
};

function Search({ selected, deleteData, qData, setQData, list }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by order id" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected Order?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [changeForm, setChangeForm] = useState(false);



    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true }
            );
            if (!dt && addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.orderDetails(data._id).then(res => {
            setData(res.data || {});

        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.orderSave(data).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                list();
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        }
    }, [open]);


    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Order'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical" disabled={!changeForm}>
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Order Id">
                                    <Input placeholder='OrderId' value={data._id} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Shipping Address">
                                    <Input placeholder='Shipping Address' value={data.shippingAddress} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Payment method">
                                    <Input placeholder='Payment method' value={data.paymentMethod} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Total Amount">
                                    <Input type="number" placeholder='order Amount' value={data.totalAmount} onChange={v => { handleChange({ totalAmount: v.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                        <Select.Option value="Initiated">Initiated</Select.Option>
                                        <Select.Option value="Dispatched">Dispatched</Select.Option>
                                        <Select.Option value="Delivered">Delivered</Select.Option>
                                        <Select.Option value="Cancelled">Cancelled</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>

            </Modal>
        </>
    );
});





const AttributeModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));

    return (
        <>
            <Modal
                title={<>Details of Order: <Typography.Text type="danger">{data._id}</Typography.Text></>}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={800}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <OrderDetail {...{ type: data.type, orderId: data._id }} />
            </Modal>
        </>
    );
});
