/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tag, Select, Tabs, Card } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../../services/jinalaya';
import jinalayaService from '../../../services/jinalaya';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import commonObj from '../../../commonObj';
import UploadImage from '../../../utils/UploadImage';
import UploadAudio from '../../../utils/UploadAudio';

const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Parshwanath108({ bhagwanId = null, jinalayaId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, isPopulate: 1, bhagwanId, jinalayaId });
    const addNewModalRef = useRef();
    const [jinalayas, setJinalayas] = useState([]);

    const columns = [
        {
            title: 'Stuti Audio',
            dataIndex: 'stutiFile',
            width: 420,
            render: (v) => {
                return <>
                    <audio controls style={{ width: 400 }}>
                        <source src={v?.url} type="audio/ogg" />
                        Your browser does not support the audio tag.
                    </audio>
                </>
            }
        },
        {
            title: 'Stuti English',
            dataIndex: 'stuti',
            // width: 400,
            render: (v) => <Typography.Paragraph ellipsis={{ expandable: true }}>{v}</Typography.Paragraph>
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this 108 parshwanath?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }

                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.list108(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.delete108(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, bhagwanId, jinalayaId }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en', bhagwanId }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);

    return (
        <>
            {
                !bhagwanId && !jinalayaId
                    ? <Typography.Title level={5}>List of 108 Parshwanath</Typography.Title>
                    : null
            }
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, bhagwanId, jinalayaId, jinalayas }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, bhagwanId, jinalayaId }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Reload</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        addAccess
                            ? <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                                <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                            </Form.Item>
                            : null
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected 108 Parshwanath?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, jinalayaId, bhagwanId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);
    const [fileList, setFileList] = useState([]);
    const [fileListAudio, setFileListAudio] = useState([]);


    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : {
                        stuti: ""
                    }
            );
            if (!dt && addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail108(data.parentId || data._id, { ln }).then(res => {
            setData(res.data || {});
            setFileList(res.data?.imgUrl ? [res.data?.imgUrl] : []);
            setFileListAudio(res.data?.stutiFile ? [res.data?.stutiFile] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.save108(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                if (ln === "en") {
                    list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
            setFileList([]);
            setFileListAudio([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ imgUrl: fileList?.[0] });
    }, [fileList]);
    useEffect(() => {
        handleChange({ stutiFile: fileListAudio?.[0] });
    }, [fileListAudio]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' 108 Parshwanath'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={ln} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setLn(v); }}
                        items={[
                            {
                                label: "English",
                                key: "en",
                                children: <Card style={{ borderTop: 0 }}>
                                    <EnForm {...{ data, handleChange, save, changeForm, fileList, setFileList, fileListAudio, setFileListAudio, jinalayaId, bhagwanId }} />
                                </Card>,
                            },
                            {
                                label: "हिन्दी",
                                key: "hi",
                                children: <Card style={{ borderTop: 0 }}>
                                    <HiForm {...{ data, handleChange, save, changeForm, fileList, setFileList, fileListAudio, setFileListAudio, jinalayaId, bhagwanId }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "ગુજરાતી",
                                key: "gu",
                                children: <Card style={{ borderTop: 0 }}>
                                    <GuForm {...{ data, handleChange, save, changeForm, fileList, setFileList, fileListAudio, setFileListAudio, jinalayaId, bhagwanId }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const EnForm = ({ data, handleChange, changeForm, fileList, setFileList, fileListAudio, setFileListAudio, jinalayaId, bhagwanId }) => {
    const [jinalayas, setJinalayas] = useState([]);

    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en', bhagwanId }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);
    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={12}>
                        <Form.Item label="Jinalaya's Photo">
                            <UploadImage {...{ fileList, setFileList }} count={1} />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Stuti Audio">
                            <UploadAudio {...{ fileList: fileListAudio, setFileList: setFileListAudio }} count={1} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Stuti English" required>
                            <Input.TextArea rows={8} placeholder="Stuti English" value={data?.stuti} onChange={e => { handleChange({ stuti: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Jinalaya" required>
                            <Select value={data.jinalayaId} onChange={v => { handleChange({ jinalayaId: v }) }} disabled={jinalayaId}>
                                {
                                    jinalayas.map(v => (
                                        <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Status" required>
                            <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                <Select.Option value={true}>Active</Select.Option>
                                <Select.Option value={false}>Inactive</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    )
}



const HiForm = ({ data, handleChange, changeForm, fileList, setFileList, fileListAudio, setFileListAudio, jinalayaId, bhagwanId }) => {
    const [jinalayas, setJinalayas] = useState([]);

    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'hi', bhagwanId }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);
    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={12}>
                        <Form.Item label="Jinalaya's Photo">
                            <UploadImage {...{ fileList, setFileList }} count={1} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Stuti Audio">
                            <UploadAudio {...{ fileList: fileListAudio, setFileList: setFileListAudio }} count={1} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Stuti Hindi" required>
                            <Input.TextArea rows={8} placeholder="Stuti Hindi" value={data?.stuti} onChange={e => { handleChange({ stuti: e.target.value }) }}  />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Jinalaya" required>
                            <Select value={data.jinalayaId} disabled >
                                {
                                    jinalayas.map(v => (
                                        <Select.Option key={v.parentId} value={v.parentId}>{v.name}</Select.Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Status" required>
                            <Select value={data.status} onChange={v => { handleChange({ status: v }) }} disabled>
                                <Select.Option value={true}>Active</Select.Option>
                                <Select.Option value={false}>Inactive</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    )
}



