/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tag, Select } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined } from '@ant-design/icons';

import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/user';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import util from '../../utils/util';
import UploadImage from '../../utils/UploadImage';
import UserProfile from './UserProfileModal';

export default function User() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const userProfileModalRef = useRef();

    const columns = [
        {
            title: 'Name',
            dataIndex: 'firstName',
            render: (value, row) => {
                return (value ?? '') + " " + (row.lastName ?? '');
            },
        },
        {
            title: 'Phone',
            dataIndex: 'phone',
            width: 150,
        },
        {
            title: 'Email',
            dataIndex: 'email',
            width: 300,
        },
        {
            title: 'Address',
            dataIndex: 'address',
            width: 300,
        },
        {
            title: 'Profile',
            dataIndex: '_id',
            width: 150,
            render: (value, row) => (
                <Button type="primary" size="small" onClick={() => { userProfileModalRef.current.openForm({ _id: row._id }) }}>User Details</Button>
            )
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this user?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ];

    function list() {
        setLoading(true);
        service.listUser(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.deleteUser(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5}>List of Users</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            // minWidth="500px"
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <UserProfileModal ref={userProfileModalRef}/>
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name / phone / email" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected users?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [fileList, setFileList] = useState([]);

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : { status: true });
            if (dt && dt.avatar) {
                setFileList([{ ...dt.avatar }]);
            } else {
                setFileList([]);
            }
        }
    }));

    const handleChange = (v, k) => { setData({ ...data, [k]: v }); }

    const save = () => {
        setAjxRequesting(true);

        service.saveUser(data).then((res) => {
            AntdMsg(res.message);
            setOpen(false);
            list();
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (!data._id) {
            handleChange(util.removeSpecialChars(data?.name || ''), 'userName');
        }
    }, [data.name])

    useEffect(() => {
        handleChange(fileList?.[0], 'avatar');
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' User'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={20}>
                                <Row gutter={[12, 2]}>
                                    <Col span={12}>
                                        <Form.Item label="First Name" required>
                                            <Input placeholder="First Name" value={data.firstName} onChange={e => { handleChange(e.target.value, 'firstName') }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Last Name" required>
                                            <Input placeholder="Last Name" value={data.lastName} onChange={e => { handleChange(util.removeSpecialChars(e.target.value), 'lastName') }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Phone" required>
                                            <Input placeholder="Phone" value={data.phone} onChange={e => { handleChange(e.target.value, 'phone') }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Email" required>
                                            <Input placeholder="Email" value={data.email} onChange={e => { handleChange((e.target.value), 'email') }} />
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>
                            <Col span={4}>
                                <Form.Item label="Avatar">
                                    <UploadImage {...{ fileList, setFileList }} count={1} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Address" required>
                                    <Input.TextArea placeholder="Address" value={data.address} onChange={e => { handleChange((e.target.value), 'address') }} />
                                </Form.Item>
                            </Col>
                            <Col span={10}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={v => { handleChange(v, 'status') }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});



const UserProfileModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));

    return (
        <>
            <Modal
                title={<>User<Typography.Text type="danger"> Profile</Typography.Text></>}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <UserProfile {...{ type: data.type, userId: data._id }}/>
            </Modal>
        </>
    );
});
