/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card, Select } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../../services/kalyanak';
import jinalayaService from '../../../services/jinalaya';
import bhagwanService from '../../../services/bhagwan';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import commonObj from '../../../commonObj';

const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Kalyanak({ bhagwanId = null, jinalayaId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, isPopulate: 1, bhagwanId, jinalayaId });
    const addNewModalRef = useRef();

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            width: 200,
            render: (v) => (
                v === 'chyavan'
                    ? 'Chyavan'
                    : v === 'janam'
                        ? 'Janam'
                        : v === 'diksha'
                            ? 'Diksha'
                            : v === 'kevalgyan'
                                ? 'Kevalgyan'
                                : v === 'nirvaan'
                                    ? 'Nirvaan'
                                    : ''
            )
        },
        {
            title: 'Bhagwan',
            dataIndex: 'jinalayaDetails',
            width: 200,
            render: (v) => v?.bhagwanDetails?.name
        },
        {
            title: 'Jinalaya',
            dataIndex: 'jinalayaDetails',
            width: 400,
            render: (v) => v?.name
        },
        {
            title: 'Tithi',
            dataIndex: 'tithi',
            width: 200,
        },
        {
            title: 'Jaap',
            dataIndex: 'jaap',
            width: 200,
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this kalyanak?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }

                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.list(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, bhagwanId, jinalayaId }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            {
                !bhagwanId && !jinalayaId
                    ? <Typography.Title level={5}>List of Kalyanaks</Typography.Title>
                    : null
            }
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, bhagwanId, jinalayaId }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, bhagwanId, jinalayaId }) {

    const [jinalayas, setJinalayas] = useState([]);
    const [bhagwan, setBhagwan] = useState([]);

    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en' }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
            bhagwanService.list({ isAll: 1, ln: 'en' }).then(res => { setBhagwan(res.data || []); }).catch(err => { AntdMsg('Bhagwan data not loaded', 'error'); });
        }
    }, []);

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select placeholder="Name" value={qData.key} onChange={e => (setQData({ ...qData, key: e }))} allowClear>
                            <Select.Option value={"chyavan"}>Chyavan</Select.Option>
                            <Select.Option value={"janam"}>Janam</Select.Option>
                            <Select.Option value={"diksha"}>Diksha</Select.Option>
                            <Select.Option value={"kevalgyan"}>Kevalgyan</Select.Option>
                            <Select.Option value={"nirvaan"}>Nirvaan</Select.Option>
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item>
                        <Select placeholder="Bhagwan" value={qData.bhagwanId} onChange={v => { setQData({ ...qData, bhagwanId: v, jinalayaId: null }) }} allowClear disabled={bhagwanId} >
                            {
                                bhagwan.map(v => (
                                    <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item>
                        <Select placeholder="Jinalaya" value={qData.jinalayaId} onChange={v => { setQData({ ...qData, jinalayaId: v }) }} allowClear disabled={jinalayaId} >
                            {
                                jinalayas.filter(v => qData.bhagwanId ? v.bhagwanId === qData.bhagwanId : true).map(v => (
                                    <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        addAccess
                            ? <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                                <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                            </Form.Item>
                            : null
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected kalyanaks?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, bhagwanId, jinalayaId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : {
                        jinalayaId,
                    }
            );
            if (!dt&& addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data.parentId || data._id, { ln }).then(res => {
            setData(res.data || {});
            setFileList(res.data?.imgUrls || []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.save(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                if (ln === "en") {
                    list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ imgUrls: fileList });
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Kalyanak'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={ln} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setLn(v); }}
                        items={[
                            {
                                label: "English",
                                key: "en",
                                children: <Card style={{ borderTop: 0 }}>
                                    <EnForm {...{ data, handleChange, save, changeForm, fileList, setFileList, bhagwanId, jinalayaId }} />
                                </Card>,
                            },
                            {
                                label: "हिन्दी",
                                key: "hi",
                                children: <Card style={{ borderTop: 0 }}>
                                    <HiForm {...{ data, handleChange, save, changeForm, fileList, setFileList, bhagwanId, jinalayaId }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "ગુજરાતી",
                                key: "gu",
                                children: <Card style={{ borderTop: 0 }}>
                                    <GuForm {...{ data, handleChange, save, changeForm, fileList, setFileList, bhagwanId, jinalayaId }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const EnForm = ({ data, handleChange, changeForm, bhagwanId, jinalayaId }) => {
    const [jinalayas, setJinalayas] = useState([]);

    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en', bhagwanId }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);

    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Name" required>
                            <Select value={data.name} onChange={v => { handleChange({ name: v }) }} >
                                <Select.Option value={"chyavan"}>Chyavan</Select.Option>
                                <Select.Option value={"janam"}>Janam</Select.Option>
                                <Select.Option value={"diksha"}>Diksha</Select.Option>
                                <Select.Option value={"kevalgyan"}>Kevalgyan</Select.Option>
                                <Select.Option value={"nirvaan"}>Nirvaan</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Jinalaya" required>
                            <Select value={data.jinalayaId} onChange={v => { handleChange({ jinalayaId: v }) }} disabled={jinalayaId}>
                                {
                                    jinalayas.map(v => (
                                        <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Tithi" required>
                            <Input placeholder="Tithi" value={data.tithi} onChange={e => { handleChange({ tithi: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Jaap" required>
                            <Input.TextArea placeholder="Jaap" value={data.jaap} onChange={e => { handleChange({ jaap: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

