/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card, Select, Divider, Radio, TimePicker, Checkbox, Tag } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined, MinusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import moment from "moment";
import service from '../../../services/shala';
import jinalayaService from '../../../services/jinalaya';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import commonObj from '../../../commonObj';
import util from '../../../utils/util';
import UploadImage from '../../../utils/UploadImage';
import Jinalaya from './Jinalaya';

const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Shala({ type, jinalayaId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, type, jinalayaId });
    const addNewModalRef = useRef();
    const jinalayaModelRef = useRef();
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            // width: 200,
        },
        {
            title: 'Unique Code',
            dataIndex: 'code',
            width: 200,
        },
        {
            title: 'Jinalaya',
            dataIndex: 'jinalayaId',
            width: 100,
            render: value => (
                <>
                    <Button type="primary" size="small" onClick={() => { jinalayaModelRef.current.openForm({ _id: value }) }} >Jinalaya</Button>
                </>
            )
        },
        {
            title: 'Labharthi',
            dataIndex: 'labharthi',
            width: 200,
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this city?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }

                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.list(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, jinalayaId }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5}>List of Data</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, type, jinalayaId }} />
            <JinalayaModel ref={jinalayaModelRef} />

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, jinalayaId }) {

    const [jinalayas, setJinalayas] = useState([]);
    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en' }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => { (setQData({ ...qData, key: e.target.value })) }} allowClear />
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item>
                        <Select placeholder="Jinalaya" value={qData.jinalayaId} onChange={v => { setQData({ ...qData, jinalayaId: v }) }} allowClear showSearch disabled={jinalayaId}>

                            {
                                jinalayas.map(v => (
                                    <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        addAccess
                            ? <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                                <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                            </Form.Item>
                            : null
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, type, jinalayaId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : {
                        type: type,
                        jinalayaId,
                        timing: { type: 'custom' },
                        contactDetails: [{}],
                        status: true,
                        parkingFacilities: [
                            {
                                type: 'rickshaw',
                                available: true,
                                remarks: ''
                            },
                            {
                                type: 'car',
                                available: true,
                                remarks: ''
                            },
                            {
                                type: 'bus',
                                available: true,
                                remarks: ''
                            }
                        ],
                        distanceFrom: [
                            {
                                type: 'busStop',
                                available: true,
                                remarks: []
                            },
                            {
                                type: 'railway',
                                available: true,
                                remarks: []
                            },
                            {
                                type: 'airport',
                                available: true,
                                remarks: []
                            }
                        ],
                        restRoomFacility: [
                            {
                                type: 'male',
                                bathroom: true,
                                toilet: true,
                                locker: true,
                            },
                            {
                                type: 'female',
                                bathroom: true,
                                toilet: true,
                                locker: true,
                            }
                        ],
                        foundation: "",
                        dharmShalaExt: {
                            acRoom: 0,
                            nonAcRoom: 0,
                            acHall: 0,
                            nonAcHall: 0,
                            totalFloor: 0,
                            liftFacilities: true
                        },
                        pathShalaExt: {
                            totalStudents: 0
                        },
                        ayambiShalaExt: {
                            facilities: [
                                {
                                    type: '12Months',
                                    available: true,
                                },
                                {
                                    type: '4Months',
                                    available: true,
                                },
                                {
                                    type: 'only2oli',
                                    available: true,
                                }
                            ]
                        }
                    }
            );
            if (!dt && addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data.parentId || data._id, { ln }).then(res => {
            setData(res.data || {});
            setFileList(res.data?.imgUrls || []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.save(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                if (ln === "en") {
                    list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ imgUrls: fileList });
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Data'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={1400}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={ln} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setLn(v); }}
                        items={[
                            {
                                label: "English",
                                key: "en",
                                children: <Card style={{ borderTop: 0 }}>
                                    <EnForm {...{ data, handleChange, save, changeForm, fileList, setFileList, jinalayaId }} />
                                </Card>,
                            },
                            {
                                label: "हिन्दी",
                                key: "hi",
                                children: <Card style={{ borderTop: 0 }}>
                                    <HiForm {...{ data, handleChange, save, changeForm, fileList, setFileList, jinalayaId }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "ગુજરાતી",
                                key: "gu",
                                children: <Card style={{ borderTop: 0 }}>
                                    <GuForm {...{ data, handleChange, save, changeForm, fileList, setFileList, jinalayaId }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const EnForm = ({ data, handleChange, changeForm, fileList, setFileList, jinalayaId }) => {

    const [jinalayas, setJinalayas] = useState([]);
    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'en' }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);

    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList, setFileList }} count={50} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Basic Details</Typography.Text></Divider>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Jinalaya" required>
                            <Select value={data.jinalayaId} onChange={v => { handleChange({ jinalayaId: v }) }} disabled={jinalayaId} >
                                {
                                    jinalayas.map(v => (
                                        <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}></Col>
                    <Col span={18}>
                        <Form.Item label="Name" required>
                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Unique Code" required>
                            <Input placeholder="Unique Code" value={data.code} onChange={e => { handleChange({ code: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Foundation Date" required>
                            <Input placeholder="Foundation Date" value={data?.foundation} onChange={e => { handleChange({ foundation: e.target.value }) }} />
                        </Form.Item>
                    </Col>

                    <Col span={6}>
                        <Form.Item label="Labharthi" required>
                            <Input placeholder="Labharthi" value={data.labharthi} onChange={e => { handleChange({ labharthi: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Prachinta" required>
                            <Select value={data.prachinta} onChange={v => { handleChange({ prachinta: v }) }}>
                                <Select.Option value="1-100">1-100</Select.Option>
                                <Select.Option value="101-500">101-500</Select.Option>
                                <Select.Option value="501-2500">501-2500</Select.Option>
                                <Select.Option value="2500-above">2500-above</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Address</Typography.Text></Divider>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Address" required>
                            <Input.TextArea placeholder="Address" value={data.address} onChange={e => { handleChange({ address: e.target.value }) }} rows={3} />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Row gutter={[12, 2]}>
                            <Col span={12}>
                                <Form.Item label="Latitude" required>
                                    <Input placeholder="Latitude" value={data?.location?.coordinates?.[0]} onChange={e => { handleChange({ [`location.coordinates.0`]: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Longitude" required>
                                    <Input placeholder="Longitude" value={data?.location?.coordinates?.[1]} onChange={e => { handleChange({ [`location.coordinates.1`]: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Button className="w-100" type="primary">Set Location</Button>
                            </Col>
                        </Row>
                    </Col>

                    <Divider orientation="left" plain={true}></Divider>
                    <Col span={11}>
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Divider orientation="left" plain={true}><Typography.Text type="danger">Contact Details</Typography.Text></Divider>
                            </Col>
                            {
                                data.contactDetails?.map((v, i) => (
                                    <React.Fragment key={i}>
                                        <Col span={10}>
                                            <Form.Item label="Name" required>
                                                <Input placeholder="Name" value={v.name} onChange={e => { handleChange({ [`contactDetails.${i}.name`]: e.target.value }) }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={10}>
                                            <Form.Item label="Number" required>
                                                <Input placeholder="Number" value={v.number} onChange={e => { handleChange({ [`contactDetails.${i}.number`]: e.target.value }) }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={4}>
                                            <Form.Item label=" " >
                                                {
                                                    i !== 0
                                                        ? <Button type="primary" danger shape="circle" icon={<MinusOutlined />} onClick={() => {
                                                            handleChange({ contactDetails: [...data.contactDetails.filter((vv, ii) => ii !== i)] });
                                                        }} />
                                                        : null
                                                }
                                                {
                                                    i + 1 === data.contactDetails?.length
                                                        ? <Button type="primary" shape="circle" icon={<PlusOutlined />} onClick={() => {
                                                            handleChange({ contactDetails: [...data.contactDetails, {}] });
                                                        }} />
                                                        : null
                                                }
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}></Col>
                                    </React.Fragment>
                                ))
                            }


                        </Row>
                    </Col>
                    <Col span={2} style={{ textAlign: "center" }}>
                        <Divider type="vertical" style={{ height: "100%" }}></Divider>
                    </Col>
                    <Col span={11}>
                        <Row gutter={[12, 8]}>
                            <Col span={24}>
                                <Divider orientation="left" plain={true}><Typography.Text type="danger">Timing</Typography.Text></Divider>
                            </Col>
                            <Col span={24}>
                                <Radio.Group
                                    options={[{ label: "Custom", value: 'custom' }, { label: "All Day", value: "allDay" }]}
                                    optionType="button"
                                    buttonStyle="solid"
                                    value={data.timing?.type}
                                    onChange={(e) => { handleChange({ 'timing.type': e.target.value, 'timing.custom': null, 'timing.allDay': null }); }}
                                />
                            </Col>
                            <Col span={24}></Col>
                            {
                                data.timing?.type === 'custom'
                                    ? <>
                                        <Col span={24}>
                                            <Form.Item label="Morning" required>
                                                <TimePicker.RangePicker className="w-100"
                                                    value={data?.timing?.custom?.morning && [moment(data?.timing?.custom?.morning?.[0]), moment(data?.timing?.custom?.morning?.[1])]}
                                                    onChange={(v) => { handleChange({ [`timing.custom.morning`]: v }); }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item label="Afternoon" required>
                                                <TimePicker.RangePicker className="w-100"
                                                    value={data?.timing?.custom?.afternoon && [moment(data?.timing?.custom?.afternoon?.[0]), moment(data?.timing?.custom?.afternoon?.[1])]}
                                                    onChange={(v) => { handleChange({ [`timing.custom.afternoon`]: v }); }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item label="Evening" required>
                                                <TimePicker.RangePicker className="w-100"
                                                    value={data?.timing?.custom?.evening && [moment(data?.timing?.custom?.evening?.[0]), moment(data?.timing?.custom?.evening?.[1])]}
                                                    onChange={(v) => { handleChange({ [`timing.custom.evening`]: v }); }} />
                                            </Form.Item>
                                        </Col>
                                    </>
                                    : <Col span={24}>
                                        <Form.Item label="Choose" required>
                                            <TimePicker.RangePicker className="w-100"
                                                value={data?.timing?.allDay && [moment(data?.timing?.allDay?.[0]), moment(data?.timing?.allDay?.[1])]}
                                                onChange={(v) => { handleChange({ [`timing.allDay`]: v }); }} />
                                        </Form.Item>
                                    </Col>
                            }

                        </Row>
                    </Col>

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Parking Facilities</Typography.Text></Divider>
                    </Col>



                    {
                        data.parkingFacilities?.map((v, i) => (
                            <Col span={8} key={i}>
                                <Form.Item label={
                                    <Checkbox checked={data?.parkingFacilities?.[i]?.available}
                                        onChange={e => {
                                            handleChange({
                                                [`parkingFacilities.${i}.available`]: e.target.checked,
                                            });
                                        }}>
                                        {
                                            data?.parkingFacilities?.[i]?.type === 'rickshaw'
                                                ? 'Rickshaw'
                                                : data?.parkingFacilities?.[i]?.type === 'car'
                                                    ? 'Car'
                                                    : data?.parkingFacilities?.[i]?.type === 'bus'
                                                        ? 'Bus'
                                                        : null
                                        }
                                    </Checkbox>}>
                                    {
                                        <Input type="text" placeholder="Remarks" value={data?.parkingFacilities?.[i]?.remarks} onChange={e => { handleChange({ [`parkingFacilities.${i}.remarks`]: e.target.value }) }} />
                                    }

                                </Form.Item>
                            </Col>
                        ))
                    }


                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Rest Room Facilities</Typography.Text></Divider>
                    </Col>

                    {
                        data.restRoomFacility?.map((v, i) => (
                            <Col span={24} key={i}>
                                <Card size="small">
                                    <Row gutter={[12, 2]}>
                                        <Col span={6}>{v.type === 'male' ? 'Male' : v.type === 'female' ? 'Female' : null}</Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.bathroom} onChange={e => {
                                                handleChange({
                                                    [`restRoomFacility.${i}.bathroom`]: e.target.checked,
                                                });
                                            }}>Bathroom</Checkbox>
                                        </Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.toilet} onChange={e => {
                                                handleChange({
                                                    [`restRoomFacility.${i}.toilet`]: e.target.checked,
                                                });
                                            }}>Toilet</Checkbox>
                                        </Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.locker} onChange={e => {
                                                handleChange({
                                                    [`restRoomFacility.${i}.locker`]: e.target.checked,
                                                });
                                            }}>Bus</Checkbox>
                                        </Col>
                                    </Row>
                                </Card>
                            </Col>
                        ))
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Distance From</Typography.Text></Divider>
                    </Col>

                    {
                        data.distanceFrom?.map((v, i) => (
                            <Col span={8} key={i}>
                                <Form.Item label={
                                    <Checkbox checked={data?.distanceFrom?.[i]?.available}
                                        onChange={e => {
                                            handleChange({
                                                [`distanceFrom.${i}.available`]: e.target.checked,
                                            });
                                        }}>
                                        {
                                            data?.distanceFrom?.[i]?.type === 'busStop'
                                                ? 'Main Busstop'
                                                : data?.distanceFrom?.[i]?.type === 'railway'
                                                    ? 'Main Railway Station'
                                                    : data?.distanceFrom?.[i]?.type === 'airport'
                                                        ? 'Nearest Airport'
                                                        : null
                                        }
                                    </Checkbox>}>
                                    <Select mode="tags" value={data?.distanceFrom?.[i]?.remarks} placeholder="Type address and press enter" onChange={v => { handleChange({ [`distanceFrom.${i}.remarks`]: v }) }} />
                                </Form.Item>
                            </Col>
                        ))
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}></Divider>
                    </Col>
                    <Col span={8}>
                        <Form.Item label={<Checkbox checked={data.upashray?.available} onChange={e => { handleChange({ [`upashray.available`]: e.target.checked }); }}>Upashray</Checkbox>}>
                            {
                                data.upashray?.available
                                    ? <Input type="text" placeholder="Number" value={data.upashray.value} onChange={e => { handleChange({ [`upashray.value`]: util.handleInteger(e.target.value) }); }} />
                                    : null
                            }

                        </Form.Item>
                    </Col>

                    {
                        data.type === 'dharmShala'
                            ? <>
                                <Col span={24}>
                                    <Divider orientation="left" plain={true}><Typography.Text type="danger">Rooms and Halls</Typography.Text></Divider>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Ac Room">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data.dharmShalaExt?.acRoom}
                                            onChange={e => { handleChange({ 'dharmShalaExt.acRoom': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Non Ac Room">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data.dharmShalaExt?.nonAcRoom}
                                            onChange={e => { handleChange({ 'dharmShalaExt.nonAcRoom': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Ac Hall">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data.dharmShalaExt?.acHall}
                                            onChange={e => { handleChange({ 'dharmShalaExt.acHall': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Non Ac Hall">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data.dharmShalaExt?.nonAcHall}
                                            onChange={e => { handleChange({ 'dharmShalaExt.nonAcHall': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Total Floor">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data?.dharmShalaExt?.totalFloor}
                                            onChange={e => { handleChange({ 'dharmShalaExt.totalFloor': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Lift Facilities Availability ">
                                        <Checkbox checked={data?.dharmShalaExt?.liftFacilities}
                                            onChange={e => {
                                                handleChange({
                                                    [`dharmShalaExt.liftFacilities`]: e.target.checked,
                                                });
                                            }}>
                                            Lift Facilities
                                        </Checkbox>
                                    </Form.Item>
                                </Col>
                            </>
                            : null
                    }
                    {
                        data.type === 'pathShala'
                            ? <>
                                <Col span={24}></Col>
                                <Col span={8}>
                                    <Form.Item label="Number of students">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.pathShalaExt?.totalStudents}
                                            onChange={e => { handleChange({ 'pathShalaExt.totalStudents': util.handleInteger(e.target.value) }); }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={24}></Col>
                            </>
                            : null
                    }
                    {
                        data.type === 'ayambiShala'
                            ? <>
                                <Col span={24}>
                                    <Divider orientation="left" plain={true}><Typography.Text type="danger">Ayambi Facilities</Typography.Text></Divider>
                                </Col>
                                {
                                    data?.ayambiShalaExt?.facilities?.map((v, i) => (
                                        <Col span={8} key={i}>
                                            <Form.Item label={
                                                <Checkbox checked={v?.available}
                                                    onChange={e => {
                                                        handleChange({
                                                            [`ayambiShalaExt.facilities.${i}.available`]: e.target.checked,
                                                        });
                                                    }}>
                                                    {
                                                        v?.type === '12Months'
                                                            ? '12 Months'
                                                            : v?.type === '4Months'
                                                                ? '4 Months'
                                                                : v?.type === 'only2oli'
                                                                    ? 'Only 2 Oli'
                                                                    : null
                                                    }
                                                </Checkbox>}>
                                            </Form.Item>
                                        </Col>
                                    ))
                                }
                            </>
                            : null
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Others Details</Typography.Text></Divider>
                    </Col>

                    <Col span={24}>
                        <Form.Item label="History">
                            <Input.TextArea type="text" placeholder="History" rows={10}
                                value={data.historyDetails}
                                onChange={e => { handleChange({ historyDetails: e.target.value }); }}
                            />
                        </Form.Item>
                    </Col>

                    <Col span={6}>
                        <Form.Item label="Status" required>
                            <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                <Select.Option value={true}>Active</Select.Option>
                                <Select.Option value={false}>Inactive</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col span={24}></Col>
                </Row>
            </Form>
        </>
    );
}

const HiForm = ({ data, handleChange, changeForm, fileList, setFileList }) => {
    const [jinalayas, setJinalayas] = useState([]);
    useEffect(() => {
        if (viewAccess) {
            jinalayaService.list({ isAll: 1, ln: 'hi' }).then(res => { setJinalayas(res.data || []); }).catch(err => { AntdMsg('Jinalaya data not loaded', 'error'); });
        }
    }, []);

    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList, setFileList }} count={50} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Basic Details</Typography.Text></Divider>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Jinalaya" required>
                            <Select value={data.jinalayaId} disabled >
                                {
                                    jinalayas.map(v => (
                                        <Select.Option key={v.parentId} value={v.parentId}>{v.name}</Select.Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}></Col>
                    <Col span={18}>
                        <Form.Item label="Name" required>
                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Unique Code" required>
                            <Input placeholder="Unique Code" value={data.code} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Foundation Date" required>
                            <Input placeholder="Foundation Date" value={data?.foundation} onChange={e => { handleChange({ foundation: e.target.value }) }} />
                        </Form.Item>
                    </Col>

                    <Col span={6}>
                        <Form.Item label="Labharthi" required>
                            <Input placeholder="Labharthi" value={data.labharthi} onChange={e => { handleChange({ labharthi: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Prachinta" required>
                            <Select value={data.prachinta} onChange={v => { handleChange({ prachinta: v }) }} disabled >
                                <Select.Option value="1-100">1-100</Select.Option>
                                <Select.Option value="101-500">101-500</Select.Option>
                                <Select.Option value="501-2500">501-2500</Select.Option>
                                <Select.Option value="2500-above">2500-above</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Address</Typography.Text></Divider>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Address" required>
                            <Input.TextArea placeholder="Address" value={data.address} onChange={e => { handleChange({ address: e.target.value }) }} rows={3} />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Row gutter={[12, 2]}>
                            <Col span={12}>
                                <Form.Item label="Latitude" required>
                                    <Input placeholder="Latitude" value={data?.location?.coordinates?.[0]} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Longitude" required>
                                    <Input placeholder="Longitude" value={data?.location?.coordinates?.[1]} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Button className="w-100" type="primary" disabled>Set Location</Button>
                            </Col>
                        </Row>
                    </Col>

                    <Divider orientation="left" plain={true}></Divider>
                    <Col span={11}>
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Divider orientation="left" plain={true}><Typography.Text type="danger">Contact Details</Typography.Text></Divider>
                            </Col>
                            {
                                data.contactDetails?.map((v, i) => (
                                    <React.Fragment key={i}>
                                        <Col span={10}>
                                            <Form.Item label="Name" required>
                                                <Input placeholder="Name" value={v.name} onChange={e => { handleChange({ [`contactDetails.${i}.name`]: e.target.value }) }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={10}>
                                            <Form.Item label="Number" required>
                                                <Input placeholder="Number" value={v.number} onChange={e => { handleChange({ [`contactDetails.${i}.number`]: e.target.value }) }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}></Col>
                                    </React.Fragment>
                                ))
                            }


                        </Row>
                    </Col>
                    <Col span={2} style={{ textAlign: "center" }}>
                        <Divider type="vertical" style={{ height: "100%" }}></Divider>
                    </Col>
                    <Col span={11}>
                        <Row gutter={[12, 8]}>
                            <Col span={24}>
                                <Divider orientation="left" plain={true}><Typography.Text type="danger">Timing</Typography.Text></Divider>
                            </Col>
                            <Col span={24}>
                                <Radio.Group
                                    options={[{ label: "Custom", value: 'custom' }, { label: "All Day", value: "allDay" }]}
                                    optionType="button"
                                    buttonStyle="solid"
                                    value={data.timing?.type}
                                    disabled
                                />
                            </Col>
                            <Col span={24}></Col>
                            {
                                data.timing?.type === 'custom'
                                    ? <>
                                        <Col span={24}>
                                            <Form.Item label="Morning" required>
                                                <TimePicker.RangePicker className="w-100" disabled
                                                    value={data?.timing?.custom?.morning && [moment(data?.timing?.custom?.morning?.[0]), moment(data?.timing?.custom?.morning?.[1])]} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item label="Afternoon" required>
                                                <TimePicker.RangePicker className="w-100" disabled
                                                    value={data?.timing?.custom?.afternoon && [moment(data?.timing?.custom?.afternoon?.[0]), moment(data?.timing?.custom?.afternoon?.[1])]} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item label="Evening" required>
                                                <TimePicker.RangePicker className="w-100" disabled
                                                    value={data?.timing?.custom?.evening && [moment(data?.timing?.custom?.evening?.[0]), moment(data?.timing?.custom?.evening?.[1])]} />
                                            </Form.Item>
                                        </Col>
                                    </>
                                    : <Col span={24}>
                                        <Form.Item label="Choose" required>
                                            <TimePicker.RangePicker className="w-100" disabled
                                                value={data?.timing?.allDay && [moment(data?.timing?.allDay?.[0]), moment(data?.timing?.allDay?.[1])]} />
                                        </Form.Item>
                                    </Col>
                            }

                        </Row>
                    </Col>

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Parking Facilities</Typography.Text></Divider>
                    </Col>



                    {
                        data.parkingFacilities?.map((v, i) => (
                            <Col span={8} key={i}>
                                <Form.Item label={
                                    <Checkbox checked={data?.parkingFacilities?.[i]?.available} disabled>
                                        {
                                            data?.parkingFacilities?.[i]?.type === 'rickshaw'
                                                ? 'Rickshaw'
                                                : data?.parkingFacilities?.[i]?.type === 'car'
                                                    ? 'Car'
                                                    : data?.parkingFacilities?.[i]?.type === 'bus'
                                                        ? 'Bus'
                                                        : null
                                        }
                                    </Checkbox>}>
                                    {
                                        +data?.parkingFacilities?.[i]?.available
                                            ? <Input type="text" placeholder="Remarks" value={data?.parkingFacilities?.[i]?.remarks} onChange={e => { handleChange({ [`parkingFacilities.${i}.remarks`]: e.target.value }) }} />
                                            : null
                                    }

                                </Form.Item>
                            </Col>
                        ))
                    }


                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Rest Room Facilities</Typography.Text></Divider>
                    </Col>

                    {
                        data.restRoomFacility?.map((v, i) => (
                            <Col span={24} key={i}>
                                <Card size="small">
                                    <Row gutter={[12, 2]}>
                                        <Col span={6}>{v.type === 'male' ? 'Male' : v.type === 'female' ? 'Female' : null}</Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.bathroom} disabled>Bathroom</Checkbox>
                                        </Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.toilet} disabled>Toilet</Checkbox>
                                        </Col>
                                        <Col span={6}>
                                            <Checkbox checked={v.locker} disabled>Bus</Checkbox>
                                        </Col>
                                    </Row>
                                </Card>
                            </Col>
                        ))
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Distance From</Typography.Text></Divider>
                    </Col>
                    {
                        data.distanceFrom?.map((v, i) => (
                            <Col span={8} key={i}>
                                <Form.Item label={
                                    <Checkbox checked={data?.distanceFrom?.[i]?.available} disabled>
                                        {
                                            data?.distanceFrom?.[i]?.type === 'busStop'
                                                ? 'Main Busstop'
                                                : data?.distanceFrom?.[i]?.type === 'railway'
                                                    ? 'Main Railway Station'
                                                    : data?.distanceFrom?.[i]?.type === 'airport'
                                                        ? 'Neares Airport'
                                                        : null
                                        }
                                    </Checkbox>}>
                                    <Select mode="tags" value={data?.distanceFrom?.[i]?.remarks} placeholder="Type address and press enter" onChange={v => { handleChange({ [`distanceFrom.${i}.remarks`]: v }) }} />
                                </Form.Item>
                            </Col>
                        ))
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}></Divider>
                    </Col>
                    <Col span={8}>
                        <Form.Item label={<Checkbox checked={data.upashray?.available} disabled>Upashray</Checkbox>}>
                            {
                                data.upashray?.available
                                    ? <Input type="text" placeholder="Number" value={data.upashray.value} disabled />
                                    : null
                            }

                        </Form.Item>
                    </Col>


                    {
                        data.type === 'dharmShala'
                            ? <>
                                <Col span={24}>
                                    <Divider orientation="left" plain={true}><Typography.Text type="danger">Rooms and Halls</Typography.Text></Divider>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Ac Room">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.dharmShalaExt?.acRoom}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Non Ac Room">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.dharmShalaExt?.nonAcRoom}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Ac Hall">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.dharmShalaExt?.acHall}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Non Ac Hall">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.dharmShalaExt?.nonAcHall}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Total Floor">
                                        <Input type="text" placeholder="Ex - 4"
                                            value={data?.dharmShalaExt?.totalFloor}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item label="Lift Facilities Availability ">
                                        <Checkbox checked={data?.dharmShalaExt?.liftFacilities}
                                            disabled>
                                            Lift Facilities
                                        </Checkbox>
                                    </Form.Item>
                                </Col>
                            </>
                            : null
                    }
                    {
                        data.type === 'pathShala'
                            ? <>
                                <Col span={24}></Col>
                                <Col span={8}>
                                    <Form.Item label="Number of students">
                                        <Input type="text" placeholder="Ex - 4" rows={10}
                                            value={data.pathShalaExt?.totalStudents}
                                            disabled
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={24}></Col>
                            </>
                            : null
                    }
                    {
                        data.type === 'ayambiShala'
                            ? <>
                                <Col span={24}>
                                    <Divider orientation="left" plain={true}><Typography.Text type="danger">Ayambi Facilities</Typography.Text></Divider>
                                </Col>
                                {
                                    data?.ayambiShalaExt?.facilities?.map((v, i) => (
                                        <Col span={8} key={i}>
                                            <Form.Item label={
                                                <Checkbox checked={v?.available} disabled>
                                                    {
                                                        v?.type === '12Months'
                                                            ? '12 Months'
                                                            : v?.type === '4Months'
                                                                ? '4 Months'
                                                                : v?.type === 'only2oli'
                                                                    ? 'Only 2 Oli'
                                                                    : null
                                                    }
                                                </Checkbox>}>
                                            </Form.Item>
                                        </Col>
                                    ))
                                }
                            </>
                            : null
                    }

                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Others Details</Typography.Text></Divider>
                    </Col>

                    <Col span={24}>
                        <Form.Item label="History">
                            <Input.TextArea type="text" placeholder="Remarks" rows={10}
                                value={data.historyDetails}
                                onChange={e => { handleChange({ historyDetails: e.target.value }); }}
                            />
                        </Form.Item>
                    </Col>

                    <Col span={6}>
                        <Form.Item label="Status" required>
                            <Select value={data.status} disabled >
                                <Select.Option value={true}>Active</Select.Option>
                                <Select.Option value={false}>Inactive</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col span={24}></Col>
                </Row>
            </Form>
        </>
    );
}

