/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card, Select, Tag } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../../services/ecomm';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import commonObj from '../../../commonObj';
import util from '../../../utils/util';
import UploadImage from '../../../utils/UploadImage';
import ProductAttribute from './ProductAttributes';
const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Products({ type, categoryId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, type, categoryId });
    const addNewModalRef = useRef();
    const attributeModalRef = useRef();


    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            // width: 200,
        },
        {
            title: 'Slug',
            dataIndex: 'slug',
            width: 220,
        },
        {
            title: 'Attributes',
            dataIndex: '_id',
            width: 150,
            render: (value, row) => (
                <Button type="primary" size="small" onClick={() => { attributeModalRef.current.openForm({ name: row.name, _id: value }) }}>Attributes</Button>
            )
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this Product?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }
                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.listProduct(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.deleteProduct(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, categoryId }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            {
                !categoryId
                    ? <Typography.Title level={5}>List of  Products</Typography.Title>
                    : null
            }

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, type, categoryId }} />
            <AttributeModal ref={attributeModalRef} />

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, categoryId }) {

    const [categories, setCategories] = useState([]);
    useEffect(() => {
        if (viewAccess) {
            service.listCategory({ isAll: 1, ln: 'en' }).then(res => { setCategories(res.data || []); }).catch(err => { AntdMsg('Categories data not loaded', 'error'); });
        }
    }, []);

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or slug" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item>
                        <Select placeholder="Category" value={qData.categoryId} onChange={v => { setQData({ ...qData, categoryId: v }) }} allowClear disabled={categoryId} >
                            {
                                categories.map(v => (
                                    <Select.Option key={v._id} value={v._id}>{v.name}</Select.Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        addAccess
                            ? <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                                <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                            </Form.Item>
                            : null
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected Product?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, categoryId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);
    const [file, setFile] = useState([]);
    const [thumbFile, setThumbFile] = useState([]);



    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true, categoryId }
            );
            if (!dt && addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detailProduct(data.parentId || data._id, { ln }).then(res => {
            setData(res.data || {});
            setFile(res.data?.imgUrls || []);
            setThumbFile(res.data?.thumbImgUrl ? [res.data?.thumbImgUrl] : []);

        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.saveProduct(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                if (ln === "en") {
                    list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
            setFile([]);
            setThumbFile([]);

        }
    }, [open]);
    useEffect(() => {
        handleChange({ imgUrls: file });
        handleChange({ imgUrl: thumbFile?.[0] });
    }, [file, thumbFile]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Product'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={ln} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setLn(v); }}
                        items={[
                            {
                                label: "English",
                                key: "en",
                                children: <Card style={{ borderTop: 0 }}>
                                    <EnForm {...{ data, handleChange, save, changeForm, categoryId, file, setFile, thumbFile, setThumbFile }} />
                                </Card>,
                            },
                            {
                                label: "हिन्दी",
                                key: "hi",
                                children: <Card style={{ borderTop: 0 }}>
                                    <HiForm {...{ data, handleChange, save, changeForm, file, setFile, thumbFile, setThumbFile }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "ગુજરાતી",
                                key: "gu",
                                children: <Card style={{ borderTop: 0 }}>
                                    <GuForm {...{ data, handleChange, save, changeForm, file, setFile, thumbFile, setThumbFile }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});


const EnForm = ({ data, handleChange, changeForm, categoryId, file, setFile, thumbFile, setThumbFile }) => {

    const [categories, setCategories] = useState([]);
    const [subCategories, setSubCategories] = useState([]);
    useEffect(() => {
        if (viewAccess) {
            service.listCategory({ isAll: 1, ln: 'en' }).then(res => { setCategories(res.data || []); }).catch(err => { AntdMsg('Categories data not loaded', 'error'); });
            service.listSubCategory({ isAll: 1, ln: 'en' }).then(res => { setSubCategories(res.data || []); }).catch(err => { AntdMsg('Sub categories data not loaded', 'error'); });
        }
    }, []);

    useEffect(() => {
        if (!data._id) {
            handleChange({ slug: util.removeSpecialChars(data?.name || '') });
        }
    }, [data.name])

    useEffect(() => {
        if (subCategories.length) {
            let filterdData = subCategories?.filter(item => data?.categoryIds?.includes(item.categoryId) && data.subCategoryIds?.includes(item._id));
            handleChange({ subCategoryIds: filterdData.map(v => v._id) || [] })
        }
    }, [data.categoryIds || subCategories]);

    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={3}>
                        <Form.Item label="Thumbnail Photo">
                            <UploadImage {...{ fileList: thumbFile, setFileList: setThumbFile }} />
                        </Form.Item>
                    </Col>
                    <Col span={21}>
                        <Form.Item label="Photo">
                            <UploadImage {...{ fileList: file, setFileList: setFile }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Choose Categories" required>
                            <Select
                                mode="multiple"
                                style={{
                                    width: '100%',
                                }}
                                placeholder="Select Categories"
                                onChange={v => { handleChange({ categoryIds: v }) }}
                                value={data.categoryIds}
                                optionLabelProp="label"
                                allowClear={true}
                            >
                                {categories.map((v) => <Select.Option key={v._id} value={v._id} label={v.name}>{v.name}</Select.Option>)}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Choose Sub Categories" required>
                            <Select
                                mode="multiple"
                                style={{
                                    width: '100%',
                                }}
                                placeholder="Select Sub Categories"
                                onChange={v => { handleChange({ subCategoryIds: v }) }}
                                value={data.subCategoryIds}
                                optionLabelProp="label"
                                allowClear={true}
                            >
                                {
                                    subCategories.filter(v => data?.categoryIds?.includes(v.categoryId)).map((v) => <Select.Option key={v._id} value={v._id} label={v.name}>{v.name}</Select.Option>)
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Row gutter={[12, 2]}>
                        <Col span={12}>
                            <Form.Item label="Name" required>
                                <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item label="Slug" required>
                                <Input placeholder="Slug" value={data.slug} onChange={e => { handleChange({ slug: util.removeSpecialChars(e.target.value) }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={24}>
                            <Form.Item label="Description" required>
                                <Input.TextArea placeholder="Write product description..." value={data.description} onChange={e => { handleChange({ description: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Price" required>
                                <Input type="text" placeholder="500" value={data.price} onChange={e => { handleChange({ price: util.handleInteger(e.target.value) }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Actual Price" required>
                                <Input type="text" placeholder="450" value={data.actualPrice} onChange={e => { handleChange({ actualPrice: util.handleInteger(e.target.value) }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="HSN code" required>
                                <Input placeholder="HSN code" value={data.hsn_code} onChange={e => { handleChange({ hsn_code: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Inventory" required>
                                <Input type="text" placeholder="Inventory" value={data.inventory} onChange={e => { handleChange({ inventory: util.handleInteger(e.target.value) }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item label="Meta Title" required>
                                <Input placeholder="Enter meta title" value={data.metaTitle} onChange={e => { handleChange({ metaTitle: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={12}>
                            <Form.Item label="Meta Tag" required>
                                <Input placeholder="Enter meta tag" value={data.metaTag} onChange={e => { handleChange({ metaTag: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={24}>
                            <Form.Item label="Meta Description" required>
                                <Input.TextArea placeholder="Write product meta description..." value={data.metaDescription} onChange={e => { handleChange({ metaDescription: e.target.value }) }} />
                            </Form.Item>
                        </Col>

                        <Col span={6}>
                            <Form.Item label="Status" required>
                                <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                    <Select.Option value={true}>Active</Select.Option>
                                    <Select.Option value={false}>Inactive</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                </Row>
            </Form>
        </>
    );
}

