import axios from "../utils/axios";

export default class service {

    static baseURL = 'jinalaya';

    static detail(id, data) {
        return axios.get(this.baseURL + '/details/' + id, { params: data });
    }
    static list(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static save(data, query) {
        return axios.post(this.baseURL + "/save", data, { params: query });
    }
    static delete(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }

    static detail108(id, data) {
        return axios.get(this.baseURL + '/108/details/' + id, { params: data });
    }
    static list108(data) {
        return axios.get(this.baseURL + '/108/list', { params: data });
    }
    static save108(data, query) {
        return axios.post(this.baseURL + '/108/save', data, { params: query });
    }
    static delete108(id) {
        return axios.post(this.baseURL + '/108/delete', { ids: id });
    }

}