import axios from "../utils/axios";

export default class service {

    static baseURL = 'job';

    static detailJobSeeker(id, data) {
        return axios.get(this.baseURL + '/seeker/details/' + id, { params: data });
    }
    static listJobSeeker(data) {
        return axios.get(this.baseURL + '/seeker/list', { params: data });
    }
    static saveJobSeeker(data, query) {
        return axios.post(this.baseURL + "/seeker/save", data, { params: query });
    }
    static deleteJobSeeker(id) {
        return axios.post(this.baseURL + '/seeker/delete', { ids: id });
    }

    static detailJob(id, data) {
        return axios.get(this.baseURL + '/details/' + id, { params: data });
    }
    static listJob(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static saveJob(data, query) {
        return axios.post(this.baseURL + "/save", data, { params: query });
    }
    static deleteJob(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }


}