import axios from "../utils/axios";

export default class service {

    static baseURL = 'music';

    //// singer-type api
    static detailSingerType(id, data) {
        return axios.get(this.baseURL + '/singer-type/details/' + id, { params: data });
    }
    static listSingerType(data) {
        return axios.get(this.baseURL + '/singer-type/list', { params: data });
    }
    static saveSingerType(data, query) {
        return axios.post(this.baseURL + "/singer-type/save", data, { params: query });
    }
    static deleteSingerType(id) {
        return axios.post(this.baseURL + '/singer-type/delete', { ids: id });
    }

    /// singer api
    static detailSinger(id, data) {
        return axios.get(this.baseURL + '/singer/details/' + id, { params: data });
    }
    static listSinger(data) {
        return axios.get(this.baseURL + '/singer/list', { params: data });
    }
    static saveSinger(data, query) {
        return axios.post(this.baseURL + "/singer/save", data, { params: query });
    }
    static deleteSinger(id) {
        return axios.post(this.baseURL + '/singer/delete', { ids: id });
    }

    /// singer file data api
    static detailSingerFileData(id, data) {
        return axios.get(this.baseURL + '/singerFileData/details/' + id, { params: data });
    }
    static listSingerFileData(data) {
        return axios.get(this.baseURL + '/singerFileData/list', { params: data });
    }
    static saveSingerFileData(data, query) {
        return axios.post(this.baseURL + "/singerFileData/save", data, { params: query });
    }
    static deleteSingerFileData(id) {
        return axios.post(this.baseURL + '/singerFileData/delete', { ids: id });
    }

    /// song-category api
    static detailSongCategory(id, data) {
        return axios.get(this.baseURL + '/song-category/details/' + id, { params: data });
    }
    static listSongCategory(data) {
        return axios.get(this.baseURL + '/song-category/list', { params: data });
    }
    static saveSongCategory(data, query) {
        return axios.post(this.baseURL + "/song-category/save", data, { params: query });
    }
    static deleteSongCategory(id) {
        return axios.post(this.baseURL + '/song-category/delete', { ids: id });
    }

     /// song api
     static detailSong(id, data) {
        return axios.get(this.baseURL + '/song/details/' + id, { params: data });
    }
    static listSong(data) {
        return axios.get(this.baseURL + '/song/list', { params: data });
    }
    static saveSong(data, query) {
        return axios.post(this.baseURL + "/song/save", data, { params: query });
    }
    static deleteSong(id) {
        return axios.post(this.baseURL + '/song/delete', { ids: id });
    }

}