const PhotosModal = forwardRef((props, ref) => {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt);
    }
  }));

  return (
    <>
      <Modal
        title={<>Attributes of <Typography.Text type="danger">{data._id}</Typography.Text></>}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onCancel={() => { setOpen(false); }}
        maskClosable={false}
        width={800}
        destroyOnClose
        className="app-modal-body-overflow"
        footer={null}
      >
        <h1>Hello Photos</h1>
      </Modal>
    </>
  );
});
