/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Tag } from 'antd';
import { DeleteOutlined } from '@ant-design/icons';
import React, { useEffect, useState, useRef } from 'react';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
// import commonObj from '../../commonObj';
import service from "../../services/ecomm"

const viewAccess = true;
// const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
// const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
// const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function UserProfileModal({ type, userId = null }) {

    // const [data, setData] = useState([]);
    const [orderData, setOrderData] = useState([])
    const [cartData, setCartData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, type, userId });
    const addNewModalRef = useRef();


    const columns = [
        {
            title: 'Product Id',
            dataIndex: 'productId',
            width: 180,


        },
        {
            title: 'Product Name',
            dataIndex: 'name',
            width: 250,
            render: (_, { productDetails }) => {
                return <span>{productDetails?.name}</span>
            }
        },
        {
            title: 'Quantity',
            dataIndex: 'quantity',
            width: 50,
        },
        // {
        //     title: 'Action',
        //     dataIndex: '_id',
        //     width: 70,
        //     hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
        //     render: (v, row) => {
        //         return <>
        //             {
        //                 deleteAccess
        //                     ? <Popconfirm
        //                         title="Are you sure to delete this product attribute?"
        //                         onConfirm={() => { deleteData(row._id); }}
        //                         okText="Yes"
        //                         cancelText="No"
        //                     >
        //                         <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
        //                     </Popconfirm>
        //                     : null
        //             }
        //             {
        //                 editAccess
        //                     ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
        //                     : null
        //             }
        //             {
        //                 !editAccess && viewAccess
        //                     ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
        //                     : null
        //             }
        //         </>
        //     }
        // },
    ].filter(item => !item.hidden);


    const columnOrder = [
        {
            title: 'Order id',
            dataIndex: '_id',
            // width: 200,
        },
        {
            title: 'Shipping Address',
            dataIndex: 'shippingAddress',
            width: 250,
        },
        {
            title: 'Payment Method',
            dataIndex: 'paymentMethod',
            width: 180,
        },
        {
            title: 'Total Amount',
            dataIndex: 'totalAmount',
            width: 150,
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: (_, { status }) => {
                return <Tag color='green'>{status}</Tag>
            },
        },
        // {
        //     title: 'Action',
        //     dataIndex: '_id',
        //     width: 70,
        //     hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
        //     render: (v, row) => {
        //         return <>
        //             {
        //                 deleteAccess
        //                     ? <Popconfirm
        //                         title="Are you sure to delete this Product?"
        //                         onConfirm={() => { deleteData(row._id); }}
        //                         okText="Yes"
        //                         cancelText="No"
        //                     >
        //                         <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
        //                     </Popconfirm>
        //                     : null
        //             }
        //             {
        //                 editAccess
        //                     ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
        //                     : null
        //             }
        //             {
        //                 !editAccess && viewAccess
        //                     ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
        //                     : null
        //             }
        //         </>
        //     }
        // },
    ].filter(item => !item.hidden);

    function listOrder() {
        if (viewAccess) {
            setLoading(true);
            service.orderList(qData).then(res => {
                setOrderData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }

    }
    function listCartData() {
        if (viewAccess) {
            setLoading(true);
            service.listCart({ userId: qData.userId }).then(res => {
                setCartData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }

    }
    const deleteData = (id) => {
        service.orderDetailDelete(id).then(res => {
            AntdMsg(res.message);
            // list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        });
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    const orderTableColoumns = columnOrder.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';
    orderTableColoumns[0].fixed = true;
    orderTableColoumns[orderTableColoumns.length - 1].fixed = 'right';

    const cartTableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData }} title="Cart" />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    const orderTableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData }} title="Order" />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        listOrder();
        listCartData();
    }, [qData.page, qData.limit]);
    return (
        <>
            <Row gutter={[16,16]}>
                <Col span={16}>
                    <div>
                        <Table
                            {...cartTableProps}
                            pagination={{ position: ['none'], pageSize: qData.limit }}
                            columns={tableColumns}
                            dataSource={cartData.length ? cartData : []}
                        />
                    </div>
                    <div>
                        <Table
                            {...orderTableProps}
                            pagination={{ position: ['none'], pageSize: qData.limit }}
                            columns={orderTableColoumns}
                            dataSource={orderData.length ? orderData : []}
                        />
                    </div>
                </Col>
                <Col span={8}>
                    <div>
                        <div className='profile-card'>
                            <div className='user-wrapper'>
                                <img src="https://www.w3schools.com/w3css/img_avatar3.png" alt="profile" />
                                <div className='user-meta-details'>
                                    <h3>username</h3>
                                    <hr />
                                    <span >wallet balance : <span>1500</span></span>
                                </div>
                            </div>
                            <div className='button-wrapper'>
                                <Button type="primary" size="large" style={{ width: "220px" }}>
                                    Subscribed
                                </Button>
                                <Button type="ghost" size="large" style={{ width: "220px" }}>
                                    Wallet history
                                </Button>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, title }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                {/* <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col> */}
                <Col span={3}>
                    <p style={{ fontSize: "1.3rem", width: "150px" }}>{title} details</p>
                </Col>
                <Col span={5}>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected product attributes ?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};