import axios from "../utils/axios";

export default class service {

    static baseURL = 'yatra';

    // for Banners : 
    static photoDetails(id, data) {
        return axios.get(this.baseURL + '/photo/details/' + id, { params: data });
    }
    static photoList(data) {
        return axios.get(this.baseURL + '/photo/list', { params: data });
    }
    static photoSave(data, query) {
        return axios.post(this.baseURL + "/photo/save", data, { params: query });
    }
    static photoDelete(id) {
        return axios.post(this.baseURL + '/photo/delete', { ids: id });
    }

}