import { AppstoreOutlined, DashboardOutlined, UserOutlined, BorderOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';
import commonObj from '../../commonObj';
import Dashboard from '../pages/Dashboard';
import Module from '../pages/Module';
import Profile from '../pages/user/Profile';
import ChangePassword from '../pages/user/ChangePassword';
import SubAdmin from '../pages/SubAdmin';
import Bhagwan from '../pages/Bhagwan';
import Jinalaya from '../pages/temple/Jinalaya';
import Shala from '../pages/temple/Shala';
import State from '../pages/location/State';
import District from '../pages/location/District';
import City from '../pages/location/City';
import Kalyanak from '../pages/temple/Kalyanak';
import Event from '../pages/Event';
import Job from '../pages/jobPortal/Job';
import JobSeeker from '../pages/jobPortal/JobSeeker';
import User from '../pages/User';

import EcommCategory from "../pages/ecomm/Category"
import EcommSubCategory from "../pages/ecomm/SubCategory"
import EcommAttribute from "../pages/ecomm/Attribute"
import EcommProduct from "../pages/ecomm/Products"
import Banner from "../pages/Banner"
import EcommOrder from "../pages/ecomm/Order"
import JainWorldImage from '../pages/jainWorld/Image';
import JainWorldVideo from '../pages/jainWorld/Video';
import SingerType from '../pages/music/SingerType';
import Singer from '../pages/music/Singer';
import SongCategory from '../pages/music/SongCategory';
import Song from '../pages/music/Song';
import Parshwanath108 from '../pages/temple/Parshwanath108';
import Setting from "../pages/Setting"
import YatraPhotos from "../pages/yatra/YatraPhotos";
const { userType } = commonObj;


const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard, hidden: userType !== 'superAdmin' },
    { title: 'Modules', icon: AppstoreOutlined, url: '/modules', component: Module, hidden: userType !== 'superAdmin' },
    { title: 'Sub Admins', icon: AppstoreOutlined, url: '/sub-admins', component: SubAdmin, hidden: userType !== 'superAdmin' },
    { title: 'Users', icon: AppstoreOutlined, url: '/users', component: User, hidden: userType !== 'superAdmin' },
    { title: 'Bhagwan', icon: AppstoreOutlined, url: '/bhagwans', component: Bhagwan, hidden: userType !== 'superAdmin' },
    { title: 'Banners', icon: ApartmentOutlined, url: '/banner', component: Banner },

    {
        title: 'Location', icon: BankOutlined, url: '/location', children: [
            { title: 'State', icon: ApartmentOutlined, url: '/', component: State },
            { title: 'District', icon: ApartmentOutlined, url: '/district', component: District },
            { title: 'City', icon: ApartmentOutlined, url: '/city', component: City },
        ]
    },
    {
        title: 'Temples', icon: BankOutlined, url: '/temples', children: [
            { title: 'Jinalaya', icon: ApartmentOutlined, url: '/', component: Jinalaya },
            { title: 'Bhojan Shala', icon: ApartmentOutlined, url: '/bhojan-shala', component: () => <Shala type="bhojanShala" /> },
            { title: 'Dharm Shala', icon: ApartmentOutlined, url: '/dharm-shala', component: () => <Shala type="dharmShala" /> },
            { title: 'Path Shala', icon: ApartmentOutlined, url: '/path-shala', component: () => <Shala type="pathShala" /> },
            { title: 'Ayambi Shala', icon: ApartmentOutlined, url: '/ayambi-shala', component: () => <Shala type="ayambiShala" /> },
            { title: 'Kalyanak', icon: ApartmentOutlined, url: '/kalyanak', component: Kalyanak },
            { title: '108 Parshwanath', icon: ApartmentOutlined, url: '/parshwanath', component: Parshwanath108 },
        ]
    },
    { title: 'Event', icon: CalendarOutlined, url: '/events', component: Event },
    { title: 'Settings', icon: CalendarOutlined, url: '/Setting', component: Setting },
    {
        title: 'Job Portal', icon: BankOutlined, url: '/job-portal', children: [
            { title: 'Job', icon: ApartmentOutlined, url: '/', component: Job },
            { title: 'Job Seeker', icon: ApartmentOutlined, url: '/job-seekrs', component: JobSeeker },
        ]
    },
    {
        title: 'Ecomm', icon: BankOutlined, url: '/ecomm', children: [
            { title: 'Category', icon: ApartmentOutlined, url: '/category', component: EcommCategory },
            { title: 'Sub Category', icon: ApartmentOutlined, url: '/sub-category', component: EcommSubCategory },
            { title: 'Attributes', icon: ApartmentOutlined, url: '/attribute', component: EcommAttribute },
            { title: 'Products', icon: ApartmentOutlined, url: '/products', component: EcommProduct },
            { title: 'Orders', icon: ApartmentOutlined, url: '/order', component: EcommOrder },
        ]
    },
    {
        title: 'Music', icon: BankOutlined, url: '/music', children: [
            { title: 'Singer Type', icon: ApartmentOutlined, url: '/singer-type', component: SingerType },
            { title: 'Singer', icon: ApartmentOutlined, url: '/singer', component: Singer },
            { title: 'Song Category', icon: ApartmentOutlined, url: '/song-category', component: SongCategory },
            { title: 'Song', icon: ApartmentOutlined, url: '/song', component: Song },
        ]
    },
    {
        title: 'Yatra', icon: BankOutlined, url: '/yatra', children: [
            { title: 'Yatra photos', icon: ApartmentOutlined, url: '/photo', component: YatraPhotos },
            
        ]
    },
    {
        title: 'Jain World', icon: BankOutlined, url: '/jain-world', children: [
            { title: 'Image', icon: ApartmentOutlined, url: '/image', component: JainWorldImage },
            { title: 'Video', icon: ApartmentOutlined, url: '/video', component: JainWorldVideo },
        ]
    },

    {
        title: 'Profile', icon: UserOutlined, url: '/profile', children: [
            { title: 'Your Profile', icon: UserOutlined, url: '/', component: Profile },
            { title: 'Change Password', icon: BorderOutlined, url: '/change-password', component: ChangePassword },
        ]
    },
].filter(v => (!v.hidden) || v.children?.length);

export default routes;