const GoogleModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
        }
    }));


    return (
        <>
            <Modal
                style={{ top: 20, height: "100%" }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}

                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <GoogleMapsModal setLat={props.setLat} setLng={props.setLng} lat={props.lat} lng={props.lng} onChange={({ lat, lng }) => props.handleChange({ [`location.coordinates.1`]: lng, [`location.coordinates.0`]: lat })} />
            </Modal>
        </>
    );
});