/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card, Space, Select, DatePicker } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined, EyeOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/bhagwan';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import commonObj from '../../commonObj';
import UploadImage from '../../utils/UploadImage';
import moment from 'moment';
import util from '../../utils/util';
import Kalyanak from './temple/Kalyanak';

const viewAccess = true;
const addAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('add');
const editAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('edit');
const deleteAccess = commonObj.userType === 'superAdmin' || commonObj.modules?.includes('delete');

export default function Bhagwan() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const kalyanakModalRef = useRef();

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            // width: 200,
        },
        {
            title: 'Kalyanak',
            dataIndex: '_id',
            width: 100,
            render: value => (
                <>
                    <Button type="primary" size="small" onClick={() => { kalyanakModalRef.current.openForm({ _id: value }) }} >Kalyanak</Button>
                </>
            )
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            hidden: !addAccess && !editAccess && !deleteAccess && !viewAccess,
            render: (v, row) => {
                return <>
                    {
                        deleteAccess
                            ? <Popconfirm
                                title="Are you sure to delete this data?"
                                onConfirm={() => { deleteData(row._id); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                            </Popconfirm>
                            : null
                    }

                    {
                        editAccess
                            ? <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                    {
                        !editAccess && viewAccess
                            ? <Button type="primary" size='small' icon={<EyeOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                            : null
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            service.list(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5}>List of Data</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <KalyanakModal ref={kalyanakModalRef} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {
                        addAccess
                            ? <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                                <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                            </Form.Item>
                            : null
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [ln, setLn] = useState('en');
    const [changeForm, setChangeForm] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : {
                        birth: {
                            type: 'bc',
                            bc: 100
                        }
                    }
            );
            if (!dt && addAccess) {
                setChangeForm(true);
            } else if (dt && editAccess) {
                setChangeForm(true);
            } else {
                setChangeForm(false);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data.parentId || data._id, { ln }).then(res => {
            setData(res.data || {});
            setFileList(res.data?.imgUrls || []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        if (changeForm) {
            setAjxRequesting(true);
            service.save(data, { ln }).then((res) => {
                AntdMsg(res.message);
                handleChange({ _id: res.data._id });
                if (ln === "en") {
                    list();
                }
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        }

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [ln, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setLn('en');
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ imgUrls: fileList });
    }, [fileList]);


    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Data'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    changeForm ? <Button key="save" type="primary" onClick={save}>Save</Button> : null,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={ln} type="card" size="small" style={{ marginBottom: 0 }} destroyInactiveTabPane onChange={(v) => { setLn(v); }}
                        items={[
                            {
                                label: "English",
                                key: "en",
                                children: <Card style={{ borderTop: 0 }}>
                                    <EnForm {...{ data, handleChange, save, changeForm, fileList, setFileList }} />
                                </Card>,
                            },
                            {
                                label: "हिन्दी",
                                key: "hi",
                                children: <Card style={{ borderTop: 0 }}>
                                    <HiForm {...{ data, handleChange, save, changeForm, fileList, setFileList }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "ગુજરાતી",
                                key: "gu",
                                children: <Card style={{ borderTop: 0 }}>
                                    <GuForm {...{ data, handleChange, save, changeForm, fileList, setFileList }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const EnForm = ({ data, handleChange, changeForm, fileList, setFileList }) => {
    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Photo">
                            <UploadImage {...{ fileList, setFileList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Name" required>
                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Birth" required>
                            <Space>
                                <Select style={{ width: "80px" }} value={data?.birth?.type}
                                    onChange={v => { handleChange({ 'birth.type': v, 'birth.ad': moment(), 'birth.bc': null }) }} >
                                    <Select.Option value="bc">BC</Select.Option>
                                    <Select.Option value="ad">AD</Select.Option>
                                </Select>
                                {
                                    data?.birth?.type === 'bc'
                                        ? <Input placeholder="Ex - 200" value={data?.birth?.bc}
                                            onChange={e => { handleChange({ 'birth.bc': util.handleInteger(e.target.value) }) }} />
                                        : <DatePicker
                                            format={'DD MMM YYYY'} value={moment(data?.birth?.ad)}
                                            onChange={(v) => { handleChange({ 'birth.ad': v }) }} allowClear={false} />
                                }
                            </Space>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const HiForm = ({ data, handleChange, changeForm, fileList, setFileList }) => {
    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Photo">
                            <UploadImage {...{ fileList, setFileList }} count={10} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Name" required>
                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Birth" required>
                            <Space>
                                <Select style={{ width: "80px" }} value={data?.birth?.type} disabled >
                                    <Select.Option value="bc">BC</Select.Option>
                                    <Select.Option value="ad">AD</Select.Option>
                                </Select>
                                {
                                    data?.birth?.type === 'bc'
                                        ? <Input placeholder="Ex - 200" value={data?.birth?.bc} disabled />
                                        : <DatePicker
                                            format={'DD MMM YYYY'} value={moment(data?.birth?.ad)} disabled />
                                }
                            </Space>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const GuForm = ({ data, handleChange, changeForm, fileList, setFileList }) => {
    return (
        <>
            <Form layout="vertical" disabled={!changeForm}>
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Photo">
                            <UploadImage {...{ fileList, setFileList }} count={10} disabled />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Name" required>
                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Birth" required>
                            <Space>
                                <Select style={{ width: "80px" }} value={data?.birth?.type} disabled >
                                    <Select.Option value="bc">BC</Select.Option>
                                    <Select.Option value="ad">AD</Select.Option>
                                </Select>
                                {
                                    data?.birth?.type === 'bc'
                                        ? <Input placeholder="Ex - 200" value={data?.birth?.bc} disabled />
                                        : <DatePicker
                                            format={'DD MMM YYYY'} value={moment(data?.birth?.ad)} disabled />
                                }
                            </Space>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}


const KalyanakModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));

    return (
        <>
            <Modal
                title="Kalyanak Details"
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <Kalyanak {...{ bhagwanId: data._id }} />
            </Modal>
        </>
    );
});